import React, { useState } from 'react'
import ThemeStyle from '../../Components/ThemeStyle'

function ApplicationPendingStatus() {
    const { bgCardColor, headingTxtTheme, paraTextTheme, btnTextColor, btnBgColor, headBgColor, titleHeadTxtTheme } = ThemeStyle()

    const [popupApplicationList, setPopupApplicationList] = useState(false)

    const handlePopupModal = () => {

        popupApplicationList ? setPopupApplicationList(false) : setPopupApplicationList(true)
    }


    const [closeModal, setCloseModal] = useState('hidden')


    const handleClose = () => {
        setPopupApplicationList(false)

    }

    return (
        <>
            <div>
                <button className='flex flex-row bg-gray-50  p-1 border border-gray-100 shadow-md' onClick={handlePopupModal}>
                    <div className=' -mt-8'>
                        <img src='https://cdn-icons-png.flaticon.com/512/1074/1074118.png' className='h-12 w-16  p-1' />

                    </div>
                    <h1 className={`${paraTextTheme} ml-2`} >Pending Application</h1>
                    <br />
                    <h1 className='mt-2 font-semibold text-gray-500 text-3xl'>0</h1>
                </button>
            </div>



            {/* ***************** modal ************************* */}
            <div className={`${popupApplicationList ? '' : 'hidden'}`}>
                <div className=' overflow-y-auto overflow-x-hidden fixed top-0 right-0 left-0 z-50 md:inset-0 h-modal md:h-full justify-center items-center bg-gray-600 opacity-95 '>

                    <section className={` w-11/12 md:w-9/12 mx-auto mt-0 md:mt-32 text-gray-600 bg-white  body-font overflow-hidden p-3   `}>
                        <div className='flex'>
                            <div className='flex-1'>
                                <h1 className="text-gray-900 text-xl title-font font-medium mb-4 flex items-center">
                                    <p className='ml-3 '>Find Holding</p>
                                </h1>
                            </div>
                            <div className='flex-1 '>
                                <button className='float-right' onClick={handleClose}>
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" class="w-8 h-8 text-red-500">
                                        <path fill-rule="evenodd" d="M12 2.25c-5.385 0-9.75 4.365-9.75 9.75s4.365 9.75 9.75 9.75 9.75-4.365 9.75-9.75S17.385 2.25 12 2.25zm-1.72 6.97a.75.75 0 10-1.06 1.06L10.94 12l-1.72 1.72a.75.75 0 101.06 1.06L12 13.06l1.72 1.72a.75.75 0 101.06-1.06L13.06 12l1.72-1.72a.75.75 0 10-1.06-1.06L12 10.94l-1.72-1.72z" clip-rule="evenodd" />
                                    </svg>
                                </button>
                            </div>
                        </div>
                    </section>

                </div>
            </div>
        </>
    )
}

export default ApplicationPendingStatus