import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useNavigate } from 'react-router-dom'
import ThemeStyle from '../../Components/ThemeStyle'
import PropertySidebar from '../Component/ModuleWiseSidebar/PropertySidebar'
import axios from 'axios'
import CitizenApplyApiList from '../../../src/Components/CitizenApplyApiList'
import BackComponent from '../../Components/BackComponent'

function PropertyDashboard(props) {



    const navigate = useNavigate()

    const [yourProperty, setyourProperty] = useState(false)




    console.log("-----PROPS applicationListByModule PROPERTY----", props.applicationList?.Property?.SAF)
    console.log("---by holding property list--------", props?.propertyApplicationList?.Holding?.applications)

    let activePropertyApplicationList = props.applicationList?.Property?.SAF
    let approvePropertyList = props.propertyApplicationList?.Holding?.applications


    return (
        <>

            <div className='-mt-8 p-2 '>
                <Link to='/dashboardEntry'>
                    <BackComponent />
                </Link>
            </div>

            {/* take a parent div ...style it as displayed below    ,....make it a grid with col-5 */}
            <div className='grid grid-cols-1 md:grid-cols-12 lg:grid-cols-12 gap-2  w-full '>

                {/* place module wise sidebar here ..it has a col-span of 1*/}
                {/* <div className='w-full col-span-2'>
                    <PropertySidebar />
                </div> */}


                {/* Rest of the component will go here ....it has a col-span of 4*/}
                <div className='col-span-10 w-full bg-gray-50 rounded-lg '>

                    {/* This message will change according to the information we want to display */}
                    <h1 className='font-mono h-10 text-3xl text-center  text-sky-500 hover:bg-sky-50 py-1 px-4  m-4'>YOUR APPLICATION  !</h1>

                    <div className='flex flex-row'>
                        <div className='text-xs bg-slate-200 px-3 py-2 ml-8 shadow-md text-gray-700 font-bold'>
                            <button>All</button>
                        </div>
                        <div className='text-xs bg-amber-300 px-3 py-2 ml-8 shadow-md text-gray-700  font-bold'>
                            <button type='button' onClick={() => setyourProperty(!yourProperty)}>Your Property</button>
                        </div>
                    </div>


                    {/* application list*/}
                    {yourProperty != true &&
                        < div className={``}>

                            {activePropertyApplicationList?.map((items) => (

                                <div class="flex bg-white shadow-lg rounded-lg mx-4 md:mx-auto my-6 max-w-2xl md:max-w-4xl ">
                                    <div class="flex items-start px-4 py-6">
                                        <img class="w-8 h-8  object-cover ml-4 shadow" src="https://cdn-icons-png.flaticon.com/512/609/609803.png" alt="avatar"
                                        />
                                        <div className=''><button className='bg-amber-500 text-gray-50 text-sm py-0 px-3 mt-9  -ml-10 rounded-sm shadow-md' onClick={() => navigate('/propApplicationDetails/' + items.application_id)}>view</button></div>
                                        <div class="pl-9">
                                            <>
                                                <div class="flex items-center justify-between">
                                                    <h2 class="text-md font-semibold text-gray-900 -mt-1">SAF NO : {items.saf_no} </h2>
                                                    <small class="text-sm text-gray-700 ml-8 font-semibold">STATUS : PENDING</small>

                                                </div>
                                            </>
                                            <div className='flex flex-row mt-2 space-x-8'>

                                                <h1 className={`text-sm text-gray-800 `}>WARD NO : 52/1</h1>
                                                <h1 className={`text-sm text-gray-800 `}>ASSESSMENT TYPE : {items.assessment_type}</h1>
                                                <p class="text-gray-700 text-sm">APPLY DATE : {items.created_at} </p>

                                            </div>
                                        </div>

                                    </div>
                                </div>
                            ))}
                        </div>
                    }

                    {/* Property list */}

                    <div className={``}>

                        {approvePropertyList?.map((items) => (

                            <div class="flex bg-white shadow-lg rounded-lg mx-4 md:mx-auto my-6 max-w-2xl md:max-w-4xl ">
                                <div class="flex items-start px-4 py-6">
                                    <img class="w-8 h-8  object-cover ml-4 shadow" src="https://cdn-icons-png.flaticon.com/512/609/609803.png" alt="avatar"
                                    />
                                    <div className=''><button className='bg-amber-500 text-gray-50 text-sm py-0 px-3 mt-9  -ml-10 rounded-sm shadow-md'onClick={() => navigate(`/holdingPropertyDetails/${items.prop_id}`)}>view</button></div>
                                    <div class="pl-9">
                                        <>
                                            <div class="flex items-center justify-between">
                                                <h2 class="text-md font-semibold text-gray-900 -mt-1">HOLDING NO : {items.new_holding_no}</h2>
                                                <small class="text-sm text-gray-700 font-semibold">STATUS: APPROVED</small>
                                            </div>
                                        </>
                                        <div className='flex flex-row mt-2 space-x-8'>

                                            <h1 className={`text-sm text-gray-800 `}>WARD NO : 52/1</h1>
                                            <h1 className={`text-sm text-gray-800 `}>ASSESSMENT TYPE : {items.assessment_type}</h1>
                                            <p class="text-gray-700 text-sm">APPLY DATE : {items.created_at} </p>

                                        </div>
                                        {items.holding_no == "" ? '' :
                                            <>

                                                <div className='flex flex-row mt-2 space-x-4 '>
                                                    <button className='text-xs bg-teal-500 text-gray-50 py-1 rounded-md px-3 shadow-lg ' onClick={() => navigate(`/safform/re/${items.prop_id}`)}>RE-ASSESSMENT</button>
                                                    <button className='text-xs bg-teal-500 text-gray-50 py-1 rounded-md px-3 shadow-lg ' onClick={() => navigate(`/safform/waterHarvesting/${items.prop_id}`)}> WATER HARVESTING</button>

                                                    <button className='text-xs bg-teal-500 text-gray-50 py-1 rounded-md px-3 shadow-lg ' onClick={() => navigate(`/con-form/${items.prop_id}`)}>CONCESSION</button>
                                                    <button className='text-xs bg-teal-500 text-gray-50 py-1 rounded-md px-3 shadow-lg ' onClick={() => navigate(`/objection/${items.prop_id}`)}>OBJECTION</button>
                                                </div>

                                            </>
                                        }
                                    </div>

                                </div>
                            </div>
                        ))}
                    </div>


                </div>
            </div>


        </>
    )
}

export default PropertyDashboard