import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import CitizenProfileComponent from '../CitizenDashboardPage/CitizenProfileComponent';
import PropertyDashboard from './PropertyDashboard';
import axios from 'axios'
import CitizenApplyApiList from '../../../src/Components/CitizenApplyApiList';
import LoaderComponent from '../Component/LoaderComponent';


function ParticularModuleApplication() {

    const { api_applicationListByModule } = CitizenApplyApiList()
    const [applicationListByModule, setapplicationListByModule] = useState()
    const [propertyApplicationList, setpropertyApplicationList] = useState()
    const [show, setshow] = useState(false)

    let module = useParams();
    console.log("module...", module.module)

    let token = window.localStorage.getItem('token')
 
    // const header = {
    //     headers:
    //     {
    //         Authorization: `Bearer ${token}`,
    //         Accept: 'application/json',
    //     }
    // }

    //{////********ApplicationList*******//////}
    const fetchApplicationList = () => {
        showLoader(true)
        axios.post(api_applicationListByModule,
            {
                module: module.module
            },
            {
                headers:
                {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json',
                }
            }
        )
            .then(function (response) {
                console.log("apllication list holding of property----- ", response.data.data);
                setapplicationListByModule(response.data.data)

                setTimeout(()=>{
                    showLoader(false)
                },2000)

            })
            .catch(function (error) {
                console.log(error);
                showLoader(false)
            })

    }

    useEffect(() => {
        fetchApplicationList();
    }, [])


     //{////********Property ApplicationList  by holding*******//////}
     const fetchPropertyListByHolding = () => {
        showLoader(true)
        axios.post(api_applicationListByModule,
            {
                module: "Holding"
            },
            {
                headers:
                {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json',
                }
            }
        )
            .then(function (response) {
                console.log("apllication list by holding 1----- ", response.data.data);
                setpropertyApplicationList(response.data.data)

                setTimeout(()=>{
                    showLoader(false)
                },2000)

            })
            .catch(function (error) {
                console.log(error);
                showLoader(false)
            })

    }

    useEffect(() => {
        fetchPropertyListByHolding();
    }, [])

    const showLoader = (value) => {
        setshow(value);
    }


    console.log("-----applicationListByModule PROPERTY----", applicationListByModule)
    console.log("-----property application list holding----", propertyApplicationList)

    return (
        <>
            <LoaderComponent show={show} />
            <div className='w-full  rounded-lg  max-auto py-4 px-4'>
                {module.module == 'Property' && <PropertyDashboard applicationList={applicationListByModule} propertyApplicationList={propertyApplicationList} showLoader={showLoader} />}
            </div>
        </>
    )
}

export default ParticularModuleApplication